	    "title": "是否可以设置访问权限？",
	    "summary": "有的资料希望只有本部门内成员可访问，应该如何设置访问权限呢？在乐享，您可以通过创建K吧，并设置K吧保密来达到资料保密的目的。第一步：创建K吧，在K吧管理中将此K吧设置为保密。第二步：将部门的成员添加到K吧中。在保密K吧中分享的所有文档活动，都只有本吧成员可见。...",
	    "is_markdown": 0,
	    "content": "<p>有的资料希望只有本部门内成员可访问，应该如何设置访问权限呢？<br /><br />在乐享，您可以通过创建K吧，并设置K吧保密来达到资料保密的目的。<br /><br /><strong>第一步：创建K吧，在K吧管理中将此K吧设置为保密</strong>。<br /><br /><img src=\"https://km.lexiangla.com/assets/7b4d0bd091e111e680a3525400c3c4b4\" alt=\"7b4d0bd091e111e680a3525400c3c4b4\" /><br /><br /><strong>第二步：将部门的成员添加到K吧中。</strong><br /><br />在保密K吧中分享的所有文档活动，都只有本吧成员可见。<br /><br /><img src=\"https://km.lexiangla.com/assets/82b453fa91e211e6a5fd525400c3c4b4\" alt=\"82b453fa91e211e6a5fd525400c3c4b4\" /></p>",
