	    "id": "3831d51691bd11e6b1cf525400c3c4b4",
	    "title": "如何申请开通乐享？",
	    "summary": "欢迎使用乐享！乐享使用的是微信企业号的帐号体系，所以在申请开通之前，请确定您的公司已经开通了微信企业号。（访问https://qy.weixin.qq.com/申请微信企业号）第一步：访问：https://lexiangla.com点击下图中的新企业注册，进入注册页面。依次填写表格中的信息，其中的企...",
	    "is_markdown": 0,
	    "content": "<p><span style=\"color:#000000;font-size:16px;font-style:normal;font-weight:normal;text-align:justify;text-indent:0px;background-color:#ffffff;\">欢迎使用乐享！乐享使用的是微信企业号的帐号体系，所以在申请开通之前，请确定您的公司已经开通了微信企业号。（<span style=\"color:#808080;\">访问<a href=\"https://qy.weixin.qq.com/\" target=\"_blank\">https://qy.weixin.qq.com/</a>申请微信企业号）</span><br /></span><br /><strong>第一步：访问：<a href=\"https://km.lexiangla.com\" target=\"_blank\">https://lexiangla.com</a> 点击下图中的新企业注册，进入注册页面。</strong><br /><br /><img alt=\"7c12c11491bd11e68383525400c3c4b4\" src=\"https://km.lexiangla.com/assets/7c12c11491bd11e68383525400c3c4b4\" /><br /><br /><span style=\"font:16px/28.8px 'Helvetica Neue', STHeiTi, 'Microsoft Yahei', '微软雅黑', sans-serif;color:#333333;text-indent:0px;background-color:#ffffff;\">依次填写表格中的信息，其中的企业号CorpID需要由企业号管理员提供，具体获取方式可查阅：</span><a href=\"http://lexiangla.net/registrations/guide\" target=\"_blank\">http://lexiangla.net/registrations/guide</a><br /><br /><img alt=\"abc27ff691bf11e6af86525400c3c4b4\" src=\"https://km.lexiangla.com/assets/abc27ff691bf11e6af86525400c3c4b4\" /><br /><br /><br /><strong>第二步：点击邮件中的链接，由企业号管理员授权应用。</strong><br /><br /><span style=\"font:16px/28.8px 'Helvetica Neue', STHeiTi, 'Microsoft Yahei', '微软雅黑', sans-serif;color:#333333;text-indent:0px;background-color:#ffffff;\">申请通过后，我们会给您发送一封邮件：【乐享】注册申请通过，请点击邮件中的链接完成注册。</span><br /><br /><span style=\"font:16px/28.8px 'Helvetica Neue', STHeiTi, 'Microsoft Yahei', '微软雅黑', sans-serif;color:#333333;text-indent:0px;background-color:#ffffff;\">点击链接后，您将跳转到微信企业号授权页面，请由公司的</span><strong>微信企业号管理员</strong><span style=\"font:16px/28.8px 'Helvetica Neue', STHeiTi, 'Microsoft Yahei', '微软雅黑', sans-serif;color:#333333;text-indent:0px;background-color:#ffffff;\">进行扫描授权。</span><br /><br /><img src=\"https://km.lexiangla.com/assets/852f1bb291db11e68456525400c3c4b4\" alt=\"852f1bb291db11e68456525400c3c4b4\" /><br /><br /><br /><strong>第三步：授权完成后，我们会进行通讯录的初始化，大约3分钟后，<a>企业号通讯录中的成员</a><a>就可以直接直接登录乐享。</a></strong><br /><br /><br /><br /></p>",
