	    "is_markdown": 0,
	    "content": "<p>1、<a href=\"https://km.lexiangla.com/teams/templates/documents/3831d51691bd11e6b1cf525400c3c4b4\" target=\"_blank\">如何申请注册乐享？ </a><br />2、如何初始化内容？<br />3、<a href=\"https://km.lexiangla.com/teams/templates/documents/9894e5d691e211e6b294525400c3c4b4\" target=\"_blank\">是否可以设置访问权限？</a>（比如资料只能本部门可见）<br />4、K吧首页可以自定义设定吗？<br />5、腾讯KM上的公司动态或者分享的知识可以同步到我们的乐享上吗？<br />6、组织架构变化，人删除再重新导入，信息会一致吗？<br />7、我在通讯录里加了一个人，为何在乐享的通讯录里没有这个人，这个人也不能登录？<br />8、K吧有哪些功能？<br />9、页面版块可以个性化设定吗？<br />10、K吧不能删除吗？<br />11、文档可以自定义可见范围吗？<br />12、乐享的文章只有创建者可以编辑吗？<br />13、可以拉取外部文章导入K吧吗？<br />14、文档如何分享给同事？<br /><br /><br /></p>",
