	    "summary": "首次进入乐享，一股熟悉的气息铺面而来，作为KM用户会感觉比较习惯。这里记录一下觉得不合适的地方，看到那里记录到哪里：1、顶部bannar姓名字体太大了，估计一般都看不全2、创建文档时没法新窗口打开，很奇怪，按照ctrl键也不行都是当页跳转3、文档的两边的边距空白太小，感觉很挤，需要加大4、活动为空的...",
	    "is_markdown": 0,
	    "content": "<p>首次进入乐享，一股熟悉的气息铺面而来，作为KM用户会感觉比较习惯。<br />这里记录一下觉得不合适的地方，看到那里记录到哪里：<br />1、顶部bannar姓名字体太大了，估计一般都看不全<br /><img src=\"https://km.lexiangla.com/assets/521d785c7fc711e6b8d5525400c3c4b4\" alt=\"521d785c7fc711e6b8d5525400c3c4b4\" /><br />2、创建文档时没法新窗口打开，很奇怪，按照ctrl键也不行都是当页跳转<br /><img src=\"https://km.lexiangla.com/assets/79a0a4267fc711e685db525400c3c4b4\" alt=\"79a0a4267fc711e685db525400c3c4b4\" /><br />3、文档的两边的边距空白太小，感觉很挤，需要加大<br /><img src=\"https://km.lexiangla.com/assets/99e81bec7fc711e68cba525400c3c4b4\" alt=\"99e81bec7fc711e68cba525400c3c4b4\" /><br />4、活动为空的时候的提示语，对用户没有引导作用<br /><img src=\"https://km.lexiangla.com/assets/b61ef0ce7fc711e68be4525400c3c4b4\" alt=\"b61ef0ce7fc711e68be4525400c3c4b4\" /><br />5、K吧公告区的初始化用语也很奇怪，建议有引导性些，“欢迎使用乐享平台”在这里出现不合适<br /><img src=\"https://km.lexiangla.com/assets/f1e915947fc711e69a93525400c3c4b4\" alt=\"f1e915947fc711e69a93525400c3c4b4\" /><br />6、首页热榜的排版后的名字很混乱<br /><img src=\"https://km.lexiangla.com/assets/ce05ea0c7fc811e69cf8525400c3c4b4\" alt=\"ce05ea0c7fc811e69cf8525400c3c4b4\" /><br />7、编写文档是，编辑框不能根据粘贴的内容内容拉下来，只看到一点点<br /><img src=\"https://km.lexiangla.com/assets/d8378f1c7fc811e6a807525400c3c4b4\" alt=\"d8378f1c7fc811e6a807525400c3c4b4\" /><br /><br /><br /></p>",
